<template>
  <va-inner-loading :loading="loading || updating">
    <div class="tab" :style="computedStyle">
      <div class="tab__create flex">
        <va-accordion>
          <va-collapse isOpenDefault :updateHeight="currentDateToggle">
            <span slot="header">
              {{ $t('dashboard.profile.tabs.transactions.create') }}
            </span>
            <div slot="body">
              <va-select
                :options="['BTC', 'LTC', 'ETH']"
                v-model="selectedCurrency"
                class="ma-0"
                :label="$t('dashboard.profile.tabs.transactions.currency')"
                no-clear
              />
              <va-input
                class="mb-3"
                :value="wallets[selectedCurrency]"
                :label="$t('dashboard.profile.tabs.transactions.balance')"
                disabled
              ></va-input>
              <va-select
                class="ma-0"
                :options="['Transfer', 'Deposit', 'Withdraw']"
                v-model="selectedAction"
                :label="$t('dashboard.profile.tabs.transactions.action')"
                no-clear
              />
              <va-select
                class="ma-0"
                :options="['Sent', 'Received']"
                v-model="selectedDirection"
                :label="$t('dashboard.profile.tabs.transactions.direction')"
                no-clear
              />

              <va-input
                class="mb-3"
                label="Amount"
                v-model="coinAmount"
              ></va-input>
              <va-checkbox
                class="mb-3"
                v-model="currentDateToggle"
                :label="$t('dashboard.profile.tabs.transactions.currentTime')"
              />
              <va-date-picker
                class="mb-3"
                v-if="!currentDateToggle"
                :label="$t('dashboard.profile.tabs.transactions.customDate')"
                v-model="customDate"
                :config="{ enableTime: true }"
              />
              <va-button
                class="mt-2 justify--center"
                small
                color="info"
                @click="createTransaction"
              >
                {{ $t('dashboard.profile.tabs.transactions.submit') }}
              </va-button>
            </div>
          </va-collapse>
          <va-collapse :updateHeight="collapseUpdateToggle">
            <span slot="header">
              {{ $t('dashboard.profile.tabs.transactions.list') }}
            </span>
            <div slot="body">
              <div class="tab__transactions pt-3 pb-2">
                <div
                  class="tab__transactions__item"
                  v-for="(transaction, i) in transactions"
                  :key="i"
                >
                  <div class="tab__transactions__item-date">
                    {{ transaction.date }}
                  </div>
                  <div class="tab__transactions__item-amount">
                    {{ transaction.amount }}
                  </div>
                  <div class="tab__transactions__item-type">
                    {{ transaction.type }}
                  </div>
                  <div class="tab__transactions__item-real">
                    {{ transaction.fake }}
                  </div>
                  <div class="tab__transactions__item-status">
                    <va-badge
                      outline
                      :color="getStatusColor(transaction.status === 'success')"
                    >
                      {{ transaction.status }}
                    </va-badge>
                  </div>
                  <va-button
                    class="justify--end"
                    small
                    flat
                    color="danger"
                    @click="deleteTransaction(transaction.id)"
                  >
                    <va-icon name="fa fa-trash" />
                  </va-button>
                </div>
              </div>
              <div class="tab__pagination_wrap" v-if="pages > 1">
                <va-pagination
                  v-model="activePage"
                  :pages="pages"
                  visiblePages="5"
                  small
                ></va-pagination>
              </div>
            </div>
          </va-collapse>
        </va-accordion>
      </div>
    </div>
  </va-inner-loading>
</template>

<script>
import {
  DeleteTransaction,
  CreateTransaction,
} from '../../../services/Transactions'

import axios from 'axios'
import _ from 'lodash'
import moment from 'moment'

export default {
  name: 'profile-transactions-tab',
  inject: ['user', 'accordion'],
  props: ['loading'],
  data: () => ({
    selectedCurrency: 'BTC',
    selectedAction: 'Transfer',
    selectedDirection: 'Sent',
    currentDateToggle: true,
    customDate: '2018-05-08 14:10',
    coinAmount: 0.01,
    updating: false,
    activePage: 1,
    collapseUpdateToggle: false,
  }),
  watch: {
    'user.transactions'(v) {
      this.collapseUpdateToggle = !this.collapseUpdateToggle
    },
    currentDateToggle(v) {
      this.accordion.onChildChange(
        this.$el.querySelector('.tab__pagination_wrap'),
        true
      )
    },
  },
  computed: {
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.updating || this.loading ? 0.2 : 1,
      }
    },
    wallets() {
      return {
        BTC: this.user.wallets.bitcoin.balance,
        LTC: this.user.wallets.litecoin.balance,
        ETH: this.user.wallets.ethereum.balance,
      }
    },
    pages() {
      return _.chunk(this.user.transactions, 6).length
    },
    transactions() {
      let result = _.chunk(this.user.transactions, 6)

      return result.length
        ? result[this.activePage - 1].map(t => {
            let global = moment(+t.unixDate).format('YYYY-MM-DD H:mm')
            let locale = moment.utc(global).toDate()

            return {
              id: t.id,
              date: moment(locale).format('YYYY-MM-DD HH:mm'),
              type: t.type,
              fake: t.fake ? 'fake' : 'real',
              status: t.status,
              amount:
                t.amount +
                ' ' +
                { Ethereum: 'ETH', Bitcoin: 'BTC', Litecoin: 'LTC' }[
                  t.currency
                ],
            }
          })
        : []
    },
  },
  methods: {
    netToCurrency: net =>
      ({
        BTC: 'bitcoin',
        LTC: 'litecoin',
        ETH: 'ethereum',
      }[net]),
    deleteTransaction(id) {
      this.updating = true

      DeleteTransaction(id, this.user.id)
        .then(({ data: transaction }) => {
          this.updating = false
          this.$root.$emit('remove-transaction', transaction)
          this.showToast('The transaction deleted', {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          this.updating = false
        })
    },
    createTransaction() {
      this.updating = true

      CreateTransaction({
        user: this.user.id,
        direction: this.selectedDirection,
        action: this.selectedAction,
        amount: +this.coinAmount,
        net: this.selectedCurrency,
        date: !this.currentDateToggle
          ? this.customDate
          : +moment(
              moment()
                .utc()
                .format('YYYY-MM-DD H:mm')
            ),
      })
        .then(({ data: transaction }) => {
          this.updating = false
          this.$root.$emit('add-transaction', transaction)

          this.showToast('The transaction was successfully emited!', {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          this.updating = false

          if (err.response) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else {
            console.log(err)
          }
        })
    },
    getStatusColor(status) {
      return ['danger', 'success'][+status]
    },
  },
}
</script>

<style lang="scss" scoped>
.no-data {
  min-height: 65px;
  padding-top: 0.5rem;
  font-size: 1.2rem;
  text-align: center;
}

.tab {
  &__create {
    justify-items: center;
  }

  &__transactions {
    display: flex;
    flex-direction: column;
    width: calc(100% + 40px);
    padding: 0 20px;
    margin: 0 -20px;

    &__item {
      display: grid;
      grid-template-columns: 1.5fr 1fr 1fr 1fr 1fr 0.1fr;
      width: 100%;

      div {
        display: grid;
        align-self: center;
        justify-self: center;
        font-size: 0.8rem;
      }
    }

    &__item:nth-child(odd) {
      background-color: #4d4d4d11;
    }
  }

  &__pagination_wrap {
    display: grid;
    justify-content: center;
    height: 2rem;
  }
}
</style>
